//@ts-nocheck
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CAIDAT } from '../service/CAIDAT';
import { HOSO } from '../service/hoso.model';
import { HosoService } from '../service/hoso.service';
import { QuillSetup } from './ho-so-editor-toolbar';

@Component({
  selector: 'app-ho-so-editor',
  templateUrl: './ho-so-editor.page.html',
  styleUrls: ['./ho-so-editor.page.scss'],
})
export class HoSoEditorPage implements OnInit {
  modules
  quillresult
  currentEditFor
  currentQuill
  hoso!: HOSO
  thisQuillInstant!
  @ViewChild("myquill") myquill: ElementRef
  buttons = [
    { text: "Thay th", icon: "repeat-outline", action: "replace" },
    { text: "Front.html", icon: "logo-html5", action: "front" },
    { text: "Back.html", icon: "logo-html5", action: "back" },
    { text: "Style.css", icon: "sparkles-outline", action: "css" }
  ]
  constructor(
    private router: Router,
    private hososervice: HosoService
  ) {
    this.currentQuill = QuillSetup.text
    this.modules = {
      toolbar: this.currentQuill.toolbar
    };
    this.ngOnInit()
  }
  ngOnInit() {
    if (this.router.getCurrentNavigation().extras.state) {
      let state = this.router.getCurrentNavigation().extras.state
      this.hoso = state.hoso

      this.changeEditor("replace")
    } else {
      // Nu khng pass state th chuyn n v trang h s
      this.router.navigate(['/t/ho-so'])
    }
  }
  
  changeEditor(type: string) {
    this.currentEditFor = type
    this.quillresult = this.hoso[type]
    if (["front", "back"].indexOf(type) > -1) {
      this.currentQuill = QuillSetup.text
      // console.log("s")
    } else if (["replace", "css"].indexOf(type) > -1) {
      this.currentQuill = QuillSetup.text
      // this.modules = {toolbar: this.currentQuill.toolbar}
    }
    // console.info("i h s ", type)
    // this.myquill["quillEditor"].setText(this.hoso[type])
    // this.myquill["quillEditor"].update()
  }
  contentChanged(e) {
    // console.log(this.quillresult)
    // console.log(this.myquill)
    if (this.quillresult != undefined) {
      this.hoso[this.currentEditFor] = this.quillresult
    }
  }
  saveChinhSua(){
    this.hososervice.updateHoSo(this.hoso, CAIDAT.hoso.saveFromEditor)
  }
  ionViewWillLeave(){
    this.saveChinhSua()
  }

}
